import React from 'react';
import Header from './components/Header/index.js';
import API from "./utils/API.js";
import Table from "./components/Table/index.js";
import './App.css';

class App extends React.Component {
  state = {
    results: []
  }

  componentDidMount() {
      API.getEmployees()
      .then(({data}) => {
        const users = data.results.map(user=> ({
          fullname: `${user.name.first} ${user.name.last}`,
          image: user.picture.medium,
          email: user.email,
          phone: user.phone,
          dob: user.dob.date
        }))
        this.setState({ results : users })
      })
  }

  render() {
    // const employee = this.state.results.map(result => ({
    //   name: result.fullname,
    //   photo: result.image,
    //   email: result.email,
    //   phone: user.phone,
    // }))
    return(
    <div className="container">
      <div>
        <Header></Header>
      </div>
      <div className="row">
          <div className="col">
            <div className="thead">
              <table>
                <thead>
                  <tr>
                    <th idName="th1">Name</th>
                    <th idName="th2">Email</th>
                    <th idName="th3">Phone</th>
                    <th idName="th4">DOB</th>
                  </tr>
                </thead>
              </table>
            </div>
            <div className="table">
              <table >
                <tbody>
                  {this.state.results.map(result=>(<Table
                    name={result.fullname}
                    email={result.email}
                    phone={result.phone}
                    dob={result.dob}
                    />
                    ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default App;